import React from 'react';
import s from './styles.module.scss';

import Img from '../../assets/images/svg/closeIcon.svg';
import { BurgerArrow } from '../../assets/icons';
import { useTranslation, Trans } from "react-i18next";

import AnchorLink from 'react-anchor-link-smooth-scroll'

const BurgerMenu = ({ isOpen }) => {
    const { t, i18n } = useTranslation();

    i18n && console.log(i18n.language, t('intro'))

    return (
        <div className={isOpen ? s.burgerMenu + ' ' + s.burgerAppear : s.burgerMenu}>
            <Trans i18nKey={"intro.burgerMenu"}>
                <nav className={s.burgerMenu__nav}>
                    <AnchorLink href={'#intro'} offset={50}>{t('home')}</AnchorLink>
                    <AnchorLink href={'#about'} offset={50}>{t('about')}</AnchorLink>
                    {/* <AnchorLink href={'#projects'} offset={50}>Projects</AnchorLink> */}
                    <AnchorLink href={'#video'} offset={50}>{t('contact')}</AnchorLink>
                </nav>
            </Trans>
            {/* <div className={s.burgerMenu__mail}>
                <p><a href={'mailto:masha.mashianova1@gmail.com'}>masha.mashianova1@gmail.com</a></p>
            </div> */}
            {/* <BurgerArrow className={s.burgerMenu__close} onClick={handleHideBurgerMenu} /> */}
            {/* <img src={Img} alt={'close_icon'} className={s.burgerMenu__close} onClick={closeFn} /> */}
        </div>
    )
}

export default BurgerMenu;