import React from 'react';
import s from './styles.module.scss';

import { useTranslation } from "react-i18next";

import { Header } from '../../pageBlocks';

const Intro = ({ openFn, open }) => {
    const { t } = useTranslation();

    t && console.log(t)

    return (
        <div id={'intro'} className={s.intro}>
            <Header openFn={openFn} open={open} />

            <div className={s.intro__description}>
                {/* Front End Developer
                Coder
                Programming */}
                {t('intro.shortTitle')}
            </div>
        </div>
    )
}

export default Intro;