import React from 'react';
import s from './styles.module.scss';

import { Github, MailBox, PhoneIcon } from '../../../assets/icons';

const VideoAndContacts = ({ lang }) => {
    return (
        <div id={'video'} className={s.video}>
            <div className={s.video__wrap}>
                <div className={s.video__wrap_video}>
                    {
                        lang === 'eng' ? <div>eng</div> : <div>ru</div>
                    }
                    <video 
                        poster={''}
                        src={''}
                    />
                </div>

                <div className={s.video__wrap_contact}>
                    {/* <p><a href={'https://github.com/asmster666'}>
                        <Github width={40} />
                        <span>github.com/asmster666</span>
                    </a></p> */}
                    <p><a href={'tel:6281213989947'}>
                        <PhoneIcon width={40} />
                        <span>+62 812 1398 9947</span>
                    </a></p>
                    <p><a href={'mailto:masha.mashianova@gmail.com'}>
                        <MailBox width={40} />
                        <span>masha.mashianova@gmail.com</span>
                    </a></p>
                </div>
            </div>

            <footer className={s.video__footer}>
                <p>Copyright &#169; 2023 Masha M. All rights reserved.</p>
            </footer>
        </div>
    )
}

export default VideoAndContacts;