import React, { useEffect } from 'react';
import s from './styles.module.scss';

import AOS from 'aos';
import 'aos/dist/aos.css';

import { useTranslation } from "react-i18next";
import { LinkIcon } from '../../../assets/icons';

const About = () => {
    const { t } = useTranslation("about");

    useEffect(() => {
        AOS.init(); 
    }, [])

    return (
        <div id={'about'} className={s.about}>
            <div className={s.about__infoAndSkills}>
                <div data-aos={'fade-right'} data-aos-delay={'500'} className={s.about__infoAndSkills_general}>
                    <div>
                        <p>{t('general.aboutName')} <span>{t('general.fullName')}</span></p>
                        <p>{t('general.aboutNationality')} <span>{t('general.nationality')}</span></p>
                        <p>{t('general.aboutLocation')} <span>{t('general.location')}</span></p>
                        <p><span><a href={`mailto:${t('general.email')}`}>{t('general.email')}</a></span></p>
                    </div>
                </div>
                <div data-aos={'fade-right'} data-aos-delay={'700'} className={s.about__infoAndSkills_skills}>
                    <h2 className={s.about__title}>Skills</h2>
                    <ul>
                        {
                            [t('skills.development')].map((item, key) => {
                                return <li key={`devTool_${key}`}>{item}</li>
                            })
                        }
                    </ul>
                    <ul>
                        {
                            [t('skills.tools')].map((item, key) => {
                                return <li key={`tool_${key}`}>{item}</li>
                            })
                        }
                    </ul>
                </div>
            </div>
            <div data-aos={'fade-left'} data-aos-delay={'500'} className={s.about__experience}>
                <h2 className={s.about__title}>{t('jobs.title')}</h2>
                    <div className={s.about__experience_jobItem + ' ' + s.about__experience_jobItem_payture}>
                        <div className={s.about__experience_jobItem_info}>
                            <h4>
                                {t('jobs.payture.title')}
                                {/* eslint-disable-next-line */}
                                <a target={'_blank'} href={'https://payture.com'}><LinkIcon width={20} /></a>
                            </h4>
                            <p className={s.about__experience_jobItem_infoPosition}>{t('jobs.payture.position')}</p>
                            <p>{t('jobs.payture.start')} 2022 - {t('jobs.payture.end')}</p>
                            <p>{t('jobs.payture.city')}</p>
                        </div>
                        <ul>
                            {
                                [t('jobs.payture.resposibilities')].map((item, key) => {
                                    return <li key={`responsibility_${key}`}>{item}</li>
                                })
                            }
                        </ul>
                    </div>
                    <div className={s.about__experience_jobItem + ' ' + s.about__experience_jobItem_mirvracha}>
                        <div className={s.about__experience_jobItem_info}>
                            <h4>
                                {t('jobs.mirvracha.title')}
                                {/* eslint-disable-next-line */}
                                <a target={'_blank'}  href={'https://mirvracha.ru'}><LinkIcon width={20} height={20} /></a>
                            </h4>
                            <p className={s.about__experience_jobItem_infoPosition}>{t('jobs.mirvracha.position')}</p>
                            <p>{t('jobs.mirvracha.start')} 2021 - {t('jobs.mirvracha.end')} 2022</p>
                            <p>{t('jobs.mirvracha.city')}</p>
                        </div>
                        <ul>
                            {
                                [t('jobs.mirvracha.resposibilities')].map((item, key) => {
                                    return <li key={`responsibility_${key}`}>{item}</li>
                                })
                            }
                        </ul>
                    </div>
                    <div className={s.about__experience_jobItem + ' ' + s.about__experience_jobItem_roomgram}>
                        <div className={s.about__experience_jobItem_info}>
                            <h4>
                                {t('jobs.roomgram.title')}
                                {/* eslint-disable-next-line */}
                                <a target={'_blank'} href={'https://roomgram.com'}><LinkIcon width={20} /></a>
                            </h4>
                            <p className={s.about__experience_jobItem_infoPosition}>{t('jobs.roomgram.position')}</p>
                            <p>{t('jobs.roomgram.start')} - {t('jobs.roomgram.end')} 2021</p>
                            <p>{t('jobs.roomgram.city')}</p>
                        </div>
                        <ul>
                            {
                                [t('jobs.roomgram.resposibilities')].map((item, key) => {
                                    return <li key={`responsibility_${key}`}>{item}</li>
                                })
                            }
                        </ul>
                    </div>
            </div>
        </div>
    )
}

export default About;