import React, { useState, useEffect } from 'react';
import s from './styles.module.scss';

import { useTranslation } from 'react-i18next';

import { BurgerIcon, BurgerArrow } from '../../../assets/icons';

const Header = ({ changeLangFn, lang, openFn, open }) => {

    const { i18n, t } = useTranslation();

    const [ activeLang, setActiveLang ] = useState(0);

    const handleChangeLang = (lang) => {
        console.log(lang)
        lang === 'eng' ? setActiveLang(1) : setActiveLang(2)
        i18n.changeLanguage(lang)
    };

    // useEffect(() => {
    //     console.log(lang)
    //     !isNaN(lang) && lang === 'eng' ? setActiveLang(1) : setActiveLang(2)
    //     console.log(activeLang, lang)
    // }, [])

    return (
        <header className={s.header}>
            <h1 className={s.header__name}>Masha</h1>
            <div className={s.header__btnWrap}> 
                <div className={s.header__btnWrap_langToggle}>
                    <span 
                        className={activeLang === 1 ? s.header__btnWrap_langToggleName + ' ' + s.header__btnWrap_langToggleActive :  s.header__btnWrap_langToggleName}
                        onClick={() => handleChangeLang('eng')}
                    >EN</span>
                    <span>|</span>
                    <span 
                        className={activeLang === 2 ? s.header__btnWrap_langToggleName + ' ' + s.header__btnWrap_langToggleActive :  s.header__btnWrap_langToggleName}
                        onClick={() => handleChangeLang('ru')}
                    >RU</span>
                </div>
                {
                    !open ?
                        <BurgerIcon 
                            className={s.header__btnWrap_burgerMenu} 
                            onClick={openFn}
                        /> :
                            <BurgerArrow 
                                className={s.header__btnWrap_burgerMenu}
                                onClick={openFn}
                            />
                }
            </div>
        </header>
    )
}

export default Header;