import React, { useState, useEffect } from 'react';
import '../styles/main.scss';

import { About, Intro, ProjectsAndSkills, VideoAndContacts } from '../components/pageBlocks';
import { BurgerMenu } from '../elements';

import { LanguageContext } from '../context';
import { useTranslation } from "react-i18next";

const Website = (props) => {

    const { i18n } = useTranslation();

    const [openBurger, setOpenBurger] = useState(false);
    //const [language, setLanguage] = useState("eng")

    const handleOpenBurgerMenu = () => {
        setOpenBurger(!openBurger)
    }

    useEffect(() => {
        i18n && i18n.changeLanguage('eng', (err, t) => {
            if (err) return console.log('something went wrong loading', err);
            t('key');
        });          
    }, [])

    // useEffect(() => {
    //     !isNaN(language) && console.log(language)
    // }, [language])

    return (
        <LanguageContext.Provider value={i18n.language}>
        <div className={'main'}>
            <BurgerMenu isOpen={openBurger} />

            <Intro 
                // changeLangFn={setLanguage}
                // lang={!isNaN(language) && language}
                openFn={handleOpenBurgerMenu} 
                open={openBurger} 
            />

            <About />

            {/* <ProjectsAndSkills /> */}

            <VideoAndContacts lang={i18n.language} />

        </div>
        </LanguageContext.Provider>
    )
}

export default Website;